@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-light.woff2") format("woff2"), url("../fonts/neuemachina-light.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-regular.woff2") format("woff2"), url("../fonts/neuemachina-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-medium.woff2") format("woff2"), url("../fonts/neuemachina-medium.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 900;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-ultrabold.woff2") format("woff2"), url("../fonts/neuemachina-ultrabold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 35px; }

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  min-height: 74px;
  padding: 25px 35px;
  background-color: #ffffff; }

.header__wrapper {
  display: flex;
  justify-items: flex-start; }

.header__logo {
  display: flex;
  align-items: center; }
  .header__logo svg {
    width: 192px;
    height: 24px; }

.loader {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 100px 0;
  background-color: #ffffff;
  transform-origin: center;
  visibility: visible;
  transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease; }
  .loader--hidden {
    z-index: -100;
    transform: scale(0);
    visibility: hidden;
    opacity: 0;
    pointer-events: none; }

.loader__logo-container {
  display: flex;
  justify-content: center;
  margin-top: auto;
  animation: loader-appeared 1s ease; }

.loader__logo {
  max-width: 80%; }
  @media (max-width: 1023px) {
    .loader__logo {
      max-width: 72%; } }

.loader__invite-text {
  margin-top: auto;
  animation: fade-in-up 0.5s ease 0.8s backwards; }
  @media (max-width: 1023px) {
    .loader__invite-text--desktop {
      display: none; } }
  .loader__invite-text--tablet {
    display: none; }
    @media (max-width: 1023px) {
      .loader__invite-text--tablet {
        display: block; } }

.loader__logo-letter {
  animation: fade-in-down 0.5s ease 0.8s backwards; }

@keyframes loader-appeared {
  from {
    transform: scale(0);
    opacity: 0; }
  to {
    transform: scale(1);
    opacity: 1; } }

@keyframes fade-in-up {
  from {
    transform: translateY(300%);
    opacity: 0; }
  to {
    transform: translateY(0);
    opacity: 1; } }

@keyframes fade-in-down {
  from {
    transform: translateY(-300%);
    opacity: 0; }
  to {
    transform: translateY(0);
    opacity: 1; } }

.hero {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  padding: 74px 0; }
  .hero h1 {
    align-self: center;
    max-width: 8.1ch;
    margin: 0 40px 0 0;
    font-weight: 400;
    font-size: 96px;
    line-height: 100%;
    font-family: "Neue Machina", "Arial", sans-serif;
    color: #2c39f2;
    transform: translateX(-150%);
    opacity: 0;
    transition: transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), opacity 0.3s ease; }
  .hero--visible h1,
  .hero--visible .hero__description {
    transform: initial;
    opacity: 1; }

.hero__background {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  min-height: 100%; }
  @media (max-width: 1023px) {
    .hero__background {
      display: none; } }
  .hero__background svg {
    width: 664px;
    height: 661px;
    opacity: 0.02;
    stroke: #000000; }

.hero__description {
  display: flex;
  flex-grow: 1;
  flex-flow: column wrap;
  justify-content: flex-start;
  align-items: flex-start;
  margin-left: auto;
  padding-top: 40px;
  transform: translateX(150%);
  opacity: 0;
  transition: transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), opacity 0.3s ease; }
  @media (max-width: 1023px) {
    .hero__description {
      position: fixed;
      top: 0;
      left: -120%;
      z-index: 100;
      justify-content: center;
      align-items: center;
      width: 100%;
      min-height: 100vh;
      min-height: calc(100 * var(--vh, 1vh));
      padding: 0;
      background-color: #2c39f2;
      transition: left 0.6s cubic-bezier(0.55, 0, 0.1, 1); }
      .hero__description.active {
        left: 0; } }

.hero__image-container {
  margin-bottom: 35px; }
  .hero__image-container .neon-blue {
    fill: #2c39f2; }
    @media (max-width: 1023px) {
      .hero__image-container .neon-blue {
        fill: #ffffff; } }
  .hero__image-container .vivid-violet {
    fill: #9627a3; }
    @media (max-width: 1023px) {
      .hero__image-container .vivid-violet {
        fill: #ffffff; } }
  .hero__image-container .torch-red {
    fill: #ff1553; }
    @media (max-width: 1023px) {
      .hero__image-container .torch-red {
        fill: #ffffff; } }

.hero__content p {
  max-width: 16ch;
  margin: 0;
  font-weight: 400;
  font-size: 30px;
  line-height: 133%;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000; }
  @media (max-width: 1023px) {
    .hero__content p {
      color: #ffffff; } }

